.App {
  background: #121212;
  margin: 0 auto;
}
.mainDiv {
  height: 100vh;
  max-width: 1920px;
  min-width: 920px;
  margin: 0 auto;
  background: linear-gradient(0deg, #221b23 30vh, #260a26 30vh);
}
.outSide {
  position: relative;
  min-width: 40%;
  max-width: 500px;
  height: auto;
  margin-right: 15vw;
  float: right;
}
.table {
  position: absolute;
  bottom: 5px;
  min-width: 920px;
}
.plant {
  width: 25%;
  min-width: 200px;
  max-width: 900px;
  height: auto;
  padding-left: 5%;
  padding-right: 3%;
}
.pixelPc {
  width: 120vw;
  min-width: 100px;
  max-width: 600px;
  height: auto;
  padding-left: 5%;
  padding-right: 3%;
}
.pixelCup {
  width: 9vw;
  min-width: 90px;
  max-width: 200px;
  height: auto;

  margin-right: 200px;
  float: right;
}

.PcScreen {
  position: absolute;
  height: auto;
  top: 0px;
  left: 40%;
}
.divTable {
  padding: 55px 0px 0px 59px;
  display: table;
  width: 100%;
}
.divTableRow {
  display: table-row;
}

.divTableCell,
.divTableHead {
  width: 50px;
  min-width: 100px;
  max-width: 600px;
  height: 50px;
  display: table-cell;
  padding: 3px 5px 3px 15px;
}
.divTableCell:hover {
  opacity: 0.7;
  cursor: grab;
}

.cvlogo {
  width: 65%;
  height: auto;
  padding-left: 5px;
}

.projectsIcon {
  width: 41%;
  height: auto;
  padding-left: 20px;
}

.linkedIn {
  width: 85%;
  height: auto;
}

.gitHubLogo {
  width: 85%;
  height: auto;
  padding-bottom: 11px;
}

.instagramLogo {
  width: 35%;
  height: auto;
}
.arrowGif{
  width: 65%;
  height: auto;
}
.cvDiv {
  height: 100%;
  width: 100%;
  max-width: 1920px;
  min-width: 920px;
  margin: 0 auto;
  margin-top: -2px;
  background: #221b23;
  padding-top: 55px;
}
.slideShowCv {
  padding-bottom: 9%;
  padding-left: 20%;
  padding-right: 20%;
}
.bonsaiPlant {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 65%;
  height: auto;
}
.projects {
  height: 100%;
  width: 100%;
  max-width: 1920px;
  min-width: 920px;
  margin: 0 auto;
  margin-top: -2px;
  background: #221b23;
}
.slideShowProjects {
  padding-bottom: 5%;
  padding-left: 20%;
  padding-right: 20%;
}

::-webkit-scrollbar {
  height: 6px;
  width: 15px;
  background: rgba(27, 26, 26, 0.2);
}
::-webkit-scrollbar-corner {
  display: none;
}
::-webkit-scrollbar-thumb {
  background: #677073;
}
